<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'basket'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ $store.state.orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in basketItems" :key="item.id">
              <h3 class="product__title--white">
                <router-link :to="{name: 'product', params: {id: item.product.id}}">{{ item.product.title }}</router-link>
              </h3>
              <b>
                {{ item.product.price | formattedPrice }} ₽
                <span><br>{{ item.quantity }} шт.</span>
              </b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ orderQuantity }}</b> {{ declOfNum(orderQuantity, ['товар', 'товара', 'товаров']) }} на сумму <b>{{ orderInfo.totalPrice | formattedPrice }}</b></p>

          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import formattedPrice from "@/helpers/formattedPrice";
import declOfNum from "@/helpers/declOfNum";

export default {
  name: 'cartInfo',
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo
    },
    orderQuantity() {
      return this.orderInfo.basket ? this.basketItems.reduce((a, b) => a + (b.quantity || 0), 0) : 0
    },
    basketItems() {
      return this.orderInfo.basket ? this.orderInfo.basket.items : {}
    }
  },
  created() {
    if(this.orderInfo && this.orderInfo === this.$route.params.id){
      return
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
  },
  filters: {
    formattedPrice
  },
  methods: {
    declOfNum
  }
}
</script>
