<template>
  <div class="form__counter" :class="className">
    <button type="button" aria-label="Убрать один товар" @click="CountMinus">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model="value" name="count" @input.prevent="CountChange">

    <button type="button" aria-label="Добавить один товар" @click="CountPlus">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {

  data(){
    return {
      value: Number(this.startValue>0 ? this.startValue : 1)
    }
  },

  props: ['className', 'startValue'],

  methods: {
    CountMinus() {
      if(this.value > 1){
        this.value--
        this.$emit('value-change', Number(this.value))
      }
    },
    CountPlus() {
      this.value++
      this.$emit('value-change', Number(this.value))
    },
    CountChange(){
      this.$emit('value-change', Number(this.value))
    }
  }

}
</script>
