<template>
  <li class="catalog__item">
    <router-link :to="{name: 'product', params: {id: product.id}}" class="catalog__pic">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <router-link :to="{name: 'product', params: {id: product.id}}">{{ product.title }}</router-link>
    </h3>

    <span class="catalog__price">{{ product.price | formattedPrice }} ₽</span>

    <product-colors :colors="product.colors" :className="'colors--black'" @color-change="productColorIdx = $event" v-if="product.colors"/>

  </li>
</template>

<script>
import formattedPrice from "@/helpers/formattedPrice";
import ProductColors from "@/components/ProductColors";
export default {
  name: 'ProductItem',

  data() {
    return {
      productColorIdx: 0
    }
  },

  components: {
    ProductColors
  },

  props: ["product"],

  filters: {
    formattedPrice
  },

  methods: {
    setProductColorIdx() {
      this.productColorIdx = this.product.colors[0].id
    }
  },

  created() {
    this.setProductColorIdx()
  }
}
</script>
