<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ totalProducts }} {{ declOfNum(totalProducts, ['товар', 'товара', 'товаров']) }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <base-loader v-if="cartSending" />
        <div class="cart__field" v-else>
          <div class="cart__data">
            <base-form-text
                title="ФИО"
                :error="formError.name"
                v-model="formData.name"
                placeholder="Введите ваше полное имя" />

            <base-form-text
                title="Адрес доставки"
                :error="formError.address"
                v-model="formData.address"
                placeholder="Введите ваш адрес" />

            <base-form-text
                title="Телефон"
                :error="formError.phone"
                v-model="formData.phone"
                type="tel"
                placeholder="Введите ваш телефон" />

            <base-form-text
                title="Email"
                :error="formError.email"
                v-model="formData.email"
                type="email"
                placeholder="Введите ваш Email" />

            <base-form-textarea
                title="Комментарий к заказу"
                :error="formError.comment"
                v-model="formData.comment"
                placeholder="Ваши пожелания" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <div class="error-msg" v-if="cartProductsLoadungFailed">
            <p>Произошла ошибка при загрузке корзины, попробуйте обновить страницу</p>
          </div>

          <base-loader v-if="cartProductsLoadung" />
          <ul class="cart__orders" v-else>
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3>{{ item.product.title }}</h3>
              <b>
                {{ item.product.price | formattedPrice }} ₽
                <span><br>{{ item.amount }} шт.</span>
              </b>
              <span>Артикул: {{ item.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ totalProducts }}</b> {{ declOfNum(totalProducts, ['товар', 'товара', 'товаров']) }} на сумму <b>{{ totalPrice | formattedPrice }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="totalProducts === 0">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>{{ formErrorMessage }}</p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";

import cartDataMixin from '@/mixins/cartDataMixin'
import axios from "axios";
import {API_BASE_URL} from "@/config";

export default {
  name: 'OrderPage',

  data() {
    return {
      formData: {},
      formError: {},
      cartSending: false,
      formErrorMessage: '',
    }
  },
  components: {
    BaseFormText,
    BaseFormTextarea
  },
  mixins: [cartDataMixin],
  methods: {
    order() {
      this.formError = {}
      this.cartSending = true
      this.formErrorMessage = ''
      axios
        .post(API_BASE_URL + '/api/orders', {
          ...this.formData
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey
          }
        })
        .then(response => {
          this.$store.commit('resetCart')
          this.$store.commit('updateOrderInfo', response.data)
          this.$router.push({name: 'orderInfo', params: {id: response.data.id}})
        })
        .catch(error => {
          this.formError = error.response.data.error.request || {}
          if(error.response.data.error.message === 'Системная ошибка')
            this.formErrorMessage = error.response.data.error.message
        })
        .then(() => {
          this.cartSending = false
        })
    }
  }
}
</script>
