<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom"
                     :price-to.sync="filterPriceTo"
                     :category-id.sync="filterCategoryId"
                     :filter-color.sync="filteredColor"/>

      <section class="catalog">
        <div v-if="productsLoadingFailed" class="error-msg">
          <p>Произошла ошибка при загрузке товаров</p>
          <button class="button button--primery" @click="loadProducts">Попробовать еще раз</button>
        </div>

        <BaseLoader  v-if="productsLoading" />
        <ProductList v-else :products="products" />
        <BasePaginator v-model="page" :count="countProducts" :per-page="productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList  from '@/components/ProductList.vue'
import BasePaginator  from '@/components/BasePagination'
import ProductFilter  from '@/components/ProductFilter'
import BaseLoader from "@/components/BaseLoader";
import axios from 'axios'
import {API_BASE_URL} from "@/config"

export default {
  name: 'MainPage',
  components: {
    ProductList,
    BasePaginator,
    ProductFilter,
    BaseLoader
  },

  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filteredColor: 0,

      page: 1,
      productsPerPage: 6,

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false
    }
  },

  computed: {
    products() {
      return this.productsData
          ? this.productsData.items.map(product => {
            return {
              ...product,
              image: product.image.file.url
            }
          })
          : null
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0
    }
  },

  methods: {
    loadProducts() {
      this.productsLoading = true
      this.productsLoadingFailed = false
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(API_BASE_URL + `/api/products`, {
              params: {
                page: this.page,
                limit: this.productsPerPage,
                categoryId: this.filterCategoryId,
                minPrice: this.filterPriceFrom,
                maxPrice: this.filterPriceTo,
                colorId: this.filteredColor,
              }
            })
            .then(response => this.productsData = response.data)
            .catch(() => this.productsLoadingFailed = true)
            .then(() => {setTimeout(() => {this.productsLoading = false}, 100)})
      }, 0)
    }
  },

  created() {
    this.loadProducts()
  },

  watch: {
    page() {
      this.loadProducts()
    },
    filterPriceFrom() {
      this.loadProducts()
    },
    filterPriceTo() {
      this.loadProducts()
    },
    filterCategoryId() {
      this.loadProducts()
    },
    filteredColor() {
      this.loadProducts()
    }
  }
}
</script>

