<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <span class="breadcrumbs__link">
            Корзина
          </span>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-if="!cartProductsLoadung">
        {{  totalProducts }} {{ declOfNum(totalProducts, ['товар', 'товара', 'товаров']) }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <div class="error-msg" v-if="cartProductsLoadungFailed">
            <p>Произошла ошибка при загрузке корзины, попробуйте обновить страницу</p>
          </div>

          <base-loader v-if="cartProductsLoadung" />
          <ul class="cart__list" v-else>
            <cart-item v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | formattedPrice }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartItem from "@/components/CartItem";
import cartDataMixin from '@/mixins/cartDataMixin'

export default {
  components: {CartItem},
  mixins: [cartDataMixin]
}
</script>
