<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      <router-link :to="{name: 'product', params: {id: item.productId}}">{{ item.product.title }}</router-link>
    </h3>

    <span class="product__code">Артикул: {{ item.productId }}</span>

    <product-counter
        :class="'product__counter'"
        @value-change="updateAmount($event)"
        :startValue="item.amount"
    />

    <b class="product__price" v-if="item.amount > 0">
      {{ (item.product.price * item.amount) | formattedPrice }} ₽
    </b>

    <button class="product__del button-del"
            type="button"
            aria-label="Удалить товар из корзины"
            @click.prevent="deleteCartProduct({productId: item.productId})">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import formattedPrice from "@/helpers/formattedPrice";
import { mapActions} from 'vuex'
import ProductCounter from "@/components/ProductCounter";
export default {
  name: 'CartItem',

  props: ['item'],

  components: {
    ProductCounter
  },

  filters: {
    formattedPrice
  },

  methods: {
    ...mapActions(['deleteCartProduct']),
    updateAmount(value){
      this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
    }
  }
}
</script>
