<template>
  <ul class="colors" :class="className">
    <li class="colors__item" v-for="itemColor in colors" :key="itemColor.id">
      <label class="colors__label">
        <input
            class="colors__radio sr-only"
            type="radio"
            :value="itemColor.id"
            @change="changeColor($event.target.value)"
            v-model="productColorIdx">
        <span class="colors__value" :style="{backgroundColor: itemColor.code}"></span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      productColorIdx: 0
    }
  },

  props: ['colors', 'className'],
  methods: {
    setProductColorIdx() {
      this.productColorIdx = this.colors ? this.colors[0].id : 0
    },
    changeColor(value){
      this.productColorIdx = Number(value)
      this.$emit('color-change', this.productColorIdx)
    }
  },

  created() {
    this.setProductColorIdx()
  }
}
</script>
